<template>
    <div>
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-5 mx-auto">
                    
                    <div class="card chatroom shadow" v-animate-css="'fadeIn'">
                        <div class="card-header">
                            <span style="color:green" v-if="isConnected">You're connected to the server!</span>
                            <span style="color:red" v-if="!isConnected">You're not connected to the server!</span>
                            <span style="color:orange" v-if="reconnect">Reconnecting...</span>
                            <br/><small>{{ informasi }}</small>
                        </div>
                        <div class="card-body" ref="scroll">
                            <ul v-if="defaultMessage.length !== 0">
                                <li v-for="pesan in defaultMessage" :key="pesan.key">
                                    <span class="pull-left default" v-if="pesan.pesan === '' && pesan.class === 'left'" v-animate-css="'fadeInUp'">
                                        <img :src="pesan.image" class="img-fluid mb-2" :alt="pesan.title" srcset="">
                                        <a target="_blank" :href="pesan.url"><p v-html="pesan.title"></p></a>
                                    </span>
                                    <span class="pull-left default" v-animate-css="'fadeInUp'" v-if="pesan.pesan !== '' && pesan.class === 'left'" v-html="pesan.pesan"></span>
                                    <div v-if="pesan.pesan !== '' && pesan.class === 'left' && startup === true" style="clear:both;max-width:60%">
                                        <span  @click="checkPertanyaan(tanya.id,tanya.urai)" class="pull-left default" v-animate-css="'fadeInUp'" v-for="tanya in pertanyaan" :key="tanya.id">
                                            <a href="javascript:;">{{ tanya.urai }}</a>
                                        </span>
                                    </div>
                                </li>       
                            </ul>
                            <ul v-if="socketMessage.length !== 0">
                                <li v-for="pesan in socketMessage" :key="pesan.key">
                                    <span class="pull-left default" v-if="pesan.pesan === '' && pesan.class === 'left'" v-animate-css="'fadeInUp'">
                                        <img :src="pesan.image" class="img-fluid mb-2" :alt="pesan.title" srcset="">
                                        <a target="_blank" :href="pesan.url"><p v-html="pesan.title"></p></a>
                                    </span>
                                    <span class="pull-left default" v-animate-css="'fadeInUp'" v-if="pesan.pesan !== '' && pesan.class === 'left'" v-html="pesan.pesan"></span>
                                    <span class="pull-right user" v-animate-css="'fadeInUp'" v-if="pesan.class === 'right'" v-html="pesan.pesan"></span>
                                </li>       
                            </ul>
                            <ul v-if="isLoading">
                                <li>
                                    <span class="pull-left default" v-animate-css="'fadeInUp'">
                                        <div class="circle-line">
                                            <div class="circle-red">&nbsp;</div>
                                            <div class="circle-blue">&nbsp;</div>
                                            <div class="circle-green">&nbsp;</div>
                                            <div class="circle-yellow">&nbsp;</div>
                                        </div>
                                    </span>
                                </li>
                            </ul>
                        </div>
                        <div class="card-footer">
                            <div class="input-group">
                                <input type="text" autofocus @keyup.enter="check" :disabled="!isConnected" class="form-control" placeholder="Mau tanya apa?" 
                                aria-label="Mau tanya apa?" v-model="pesan">
                                <div class="input-group-append">
                                    <button class="btn btn-primary" @click="check" :disabled="!isConnected" type="button">
                                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24" xml:space="preserve"><path fill="#d7d7d7" d="M22,11.7V12h-0.1c-0.1,1-17.7,9.5-18.8,9.1c-1.1-0.4,2.4-6.7,3-7.5C6.8,12.9,17.1,12,17.1,12H17c0,0,0-0.2,0-0.2c0,0,0,0,0,0c0-0.4-10.2-1-10.8-1.7c-0.6-0.7-4-7.1-3-7.5C4.3,2.1,22,10.5,22,11.7z"></path></svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="waves">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#0099ff" fill-opacity="1" d="M0,64L11.4,64C22.9,64,46,64,69,80C91.4,96,114,128,137,144C160,160,183,160,206,138.7C228.6,117,251,75,274,101.3C297.1,128,320,224,343,250.7C365.7,277,389,235,411,186.7C434.3,139,457,85,480,58.7C502.9,32,526,32,549,42.7C571.4,53,594,75,617,101.3C640,128,663,160,686,170.7C708.6,181,731,171,754,154.7C777.1,139,800,117,823,106.7C845.7,96,869,96,891,128C914.3,160,937,224,960,245.3C982.9,267,1006,245,1029,202.7C1051.4,160,1074,96,1097,101.3C1120,107,1143,181,1166,186.7C1188.6,192,1211,128,1234,138.7C1257.1,149,1280,235,1303,240C1325.7,245,1349,171,1371,144C1394.3,117,1417,139,1429,149.3L1440,160L1440,320L1428.6,320C1417.1,320,1394,320,1371,320C1348.6,320,1326,320,1303,320C1280,320,1257,320,1234,320C1211.4,320,1189,320,1166,320C1142.9,320,1120,320,1097,320C1074.3,320,1051,320,1029,320C1005.7,320,983,320,960,320C937.1,320,914,320,891,320C868.6,320,846,320,823,320C800,320,777,320,754,320C731.4,320,709,320,686,320C662.9,320,640,320,617,320C594.3,320,571,320,549,320C525.7,320,503,320,480,320C457.1,320,434,320,411,320C388.6,320,366,320,343,320C320,320,297,320,274,320C251.4,320,229,320,206,320C182.9,320,160,320,137,320C114.3,320,91,320,69,320C45.7,320,23,320,11,320L0,320Z"></path></svg>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { 
            pertanyaan, 
            generateToken, 
            token, 
            perhitunganBeratBadanPria, 
            perhitunganBeratBadanWanita,
            pertanyaanJawabanDefault
        } from './helper'

export default {
    data() {
        return {
            isConnected: false,
            reconnect: false,
            socketMessage: [],
            apiMessage: [],
            pesan: '',
            welcome: '',
            key: '3998cf1301764f0aae57b736a9521513',
            index: 1000,
            informasi: '',
            startup: false,
            pertanyaan: pertanyaan,
            defaultMessage: [],
            isLoading: false
        }
    },
    sockets:{
        connect() {
            this.reconnect = false
            this.isConnected = true;
            sessionStorage.setItem("token", this.$socket.id)
            fetch(`https://ipinfo.io/?token=4a97ed6793d20b`)
            .then(response => response.json())
            .then(data => {
                    localStorage.setItem("ip", data.ip)
                    localStorage.setItem("city", data.city)
                    this.informasi = `IP : ${data.ip}, Lokasi : ${data.city}`
                    if(!this.startup) {
                        this.defaultMessage.splice(0, this.defaultMessage.length)
                        let pesan = `Selamat datang. 
                                    Saya dengan Indira. Ada yang bisa dibantu?
                                    <br/>Berikut adalah beberapa pertanyaan default dari kami :`
                        this.defaultMessage.push({
                            key:token(),
                            pesan: pesan,
                            class: 'left'
                        })

                    }
                    this.startup = true
            });
        },
        reconnect() {
            this.reconnect = true
            this.isConnected = false;
        },
        disconnect() {
            this.isConnected = false;
        },
        sendMessage(data) {
            this.socketMessage.push({
                key: generateToken(),
                pesan: data,
                class: 'right'
            })
        },
        receiveMessage(data) {
            this.isLoading = true
            setTimeout(() => {
                this.socketMessage.push({
                    key: generateToken(),
                    pesan: data,
                    class: 'left'
                })
                this.isLoading = false
            }, 5000)
        }
    },
    methods: {
        async getDataFromAPI(param = 'apple') {
            const { data } = await axios.get(`https://newsapi.org/v2/everything?q=${param}&from=2021-03-19&to=2021-03-19&sortBy=popularity&apiKey=${this.key}`)
            if(data.articles.length > 0) {
                for(let art in data.articles) {
                    this.socketMessage.push({
                        key: generateToken(),
                        title: data.articles[art].title,
                        image: data.articles[art].urlToImage,
                        url: data.articles[art].url,
                        class: 'left',
                        pesan: ''
                    })
                    this.scrollToEnd()
                }
            }
        },
        check() {
            if(this.pesan !== '') {

                this.$socket.emit('sendMessage', {
                    token: sessionStorage.getItem("token"),
                    pesan: this.pesan
                })
                
                if(parseInt(sessionStorage.getItem('tanya')) !== 1) {
                    let jawab;
                    let found = false
                    this.pesan = this.pesan.toLowerCase()
                    for(let i in pertanyaanJawabanDefault) {
                        if(this.pesan.includes(pertanyaanJawabanDefault[i].key) || pertanyaanJawabanDefault[i].key.includes(this.pesan)) {
                            jawab = pertanyaanJawabanDefault[i].jawaban
                            found = true
                        }
                        if(this.pesan.indexOf(pertanyaanJawabanDefault[i].key) !== -1 || pertanyaanJawabanDefault[i].key.indexOf(this.pesan) !== -1) {
                            jawab = pertanyaanJawabanDefault[i].jawaban
                            found = true
                        }
                    }

                    if(!found) {
                        jawab = 'Mohon maaf. Untuk saat ini pertanyaan kamu tidak ada di sistem kami.' 
                    }
    
                    this.$socket.emit('receiveMessage', {
                        token: sessionStorage.getItem("token"),
                        pesan: jawab
                    })
                }

                if(parseInt(sessionStorage.getItem('tanya')) === 1) {
                    this.$socket.emit('receiveMessage', {
                        token: sessionStorage.getItem("token"),
                        pesan: `Hai kak! Jika kamu laki-laki berat badan ideal kamu adalah <b>${perhitunganBeratBadanPria(parseInt(this.pesan))} kg</b>.
                                Jika kamu perempuan berat badan ideal kamu adalah <b>${perhitunganBeratBadanWanita(parseInt(this.pesan))} kg</b>.`
                    })
                    sessionStorage.removeItem("tanya")
                } 
                

                this.pesan = ''

            }
        },
        checkPertanyaan(id,urai) {
            
            this.socketMessage.push({
                key: generateToken(),
                pesan: urai,
                class:'right'
            })

            let tanya
            let found = false
            for(let d in pertanyaan) {
                if(pertanyaan[d].id === parseInt(id)) {
                    if(pertanyaan[d].tanya !== undefined) {
                        tanya = pertanyaan[d].tanya
                        found  = true
                    }
                }
            }
            
            if(!found) {
                tanya = 'Yaaahhh... Kategori ini belum ada di sistem kami.'
            }

            sessionStorage.setItem('tanya', parseInt(id))
            this.isLoading = true
            setTimeout(() => {
                this.socketMessage.push({
                    key: generateToken(),
                    pesan: tanya,
                    class:'left'
                })
                this.isLoading = false
            }, 3000)
        },
        scrollToEnd () {
            var content = this.$refs.scroll;
            content.scrollTop = content.scrollHeight;
        }
    },
    created() {
        sessionStorage.removeItem("tanya")
    },
    updated () {
        this.scrollToEnd()
    }
}
</script>