import Vue from 'vue'
import App from './Chatbot.vue'
import VueSocketIO from 'vue-socket.io'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import VAnimateCss from 'v-animate-css';
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import '@/assets/style.css'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faPaperPlane, faAngleDoubleRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faPaperPlane,faAngleDoubleRight)

Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.use(VAnimateCss);

Vue.config.productionTip = false
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.use(new VueSocketIO({
  debug: false,
  connection: 'https://server-chatbotv1.herokuapp.com/',
  // connection: 'http://localhost:1200/',
  options: {
    secure: true,
    transports: ['websocket', 'polling']
  }
}))

new Vue({
  render: h => h(App),
}).$mount('#app')
